main {
    height: 100%;
    background-color: var(--secondary);
    padding-top: 3em;
}

.flip-board {
    background-color: transparent;
    height: 490px;
    width: 960px;
}

.flip-board-inner, .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transform-style: preserve-3d;
}

.flip-board-inner {
    transition: transform 1.5s;
}

.flip-card-inner {
    transition: transform 1s;
}

.flip-board-inner.is-flipped {
    transform: rotateX(180deg);
}

.flip-board-front {
    transform: translateZ(1px);
}

.flip-board-front, .flip-board-back, .flip-card-front, .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border-radius: 10px;
    box-shadow: 3px 4px 8px 0 rgba(0, 0, 0, 0.3), 3px 6px 20px 0 rgba(0, 0, 0, 0.28);
}

.flip-board-back, .flip-card-back {
    background-color: var(--primary);
    color: var(--quaternary);
    border-radius: 10px;
}

.flip-board-back {
    transform: translateZ(-1px) rotateX(180deg);
}

.flip-card-back {
    transform: rotateY(180deg);
}


.gameboard {
    flex-wrap: wrap;
    height: fit-content;
    background-color: var(--quinary);
    border: 5px solid var(--quaternary);
    border-radius: 10px;
}

.flip-card {
    margin: 20px 20px;
    height: 200px;
    width: 150px;
    background-color: transparent;
}

.flip-card-inner.is-flipped {
    transform: rotateY(180deg);
}

.flip-card-front img {
    height: 100%;
    width: 100%;
    border-radius: 10px;
}
.gameover {
    justify-content: center;
    height: 100%;
    color: var(--primary);
    background-color: var(--quinary);
    border: 4px solid var(--quaternary);
    border-radius: 10px;
}

.gameover p {
    margin: 1em 0;
    font-size: 24px;
}

.gameover button {
    width: fit-content;
    padding: 0.4em 0.7em;
    font-size: 20px;
    font-family: 'Overpass', sans-serif;
    color: var(--quinary);
    background-color: var(--primary);
    border: 2px solid var(--quaternary);
    border-radius: 5px;
}